body {
    background-color: #00ffbf;
    margin: 0;
}

.container{
    width:550px;
    height:670px;
    margin:auto;
    margin-top: 45px;
    border-radius: 12px;
    background-image: linear-gradient(180deg,#130754 0%,#3b2f80 100%);
}
.top-bar{
    display: flex;
    justify-content: center;
    gap:10px;
    padding-top:40px;
}
.top-bar input{
    display:flex;
    width:340px;
    height:55px;
    background: white;
    border:none;
    outline: none;
    border-radius: 40px;
    padding-left: 30px;
    color:#626262;
    font-size:15px;
    font-weight: 400;
}
.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width:78px;
    height:55px;
    background: white;
    border-radius: 40px;
    cursor:pointer;
}
.weather-image{
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400px;
}
.weather-location{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400px;
}
.data-container{
    margin-top: 50px;
    color: white;
    display: flex;
    justify-content: center;
}
.element{
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap:12px;
}
.data{
    font-size: 34px;
    font-weight: 400;
}
.text{
    font-size: 20px;
    font-weight: 400;
}
.icon{
    margin-top: 6px;
}